.social-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 21px;
  max-width: 150px;
  width: 100%;

  &__link {
    height: 35px;
    width: 35px;

    img {
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }

  @include w($lg) {
    margin: 0 auto;
    justify-content: center;
  }
}
